.navbar-nav{
    padding: 7px;
    background: -webkit-gradient(linear, left bottom, left top, from(#9b1827e5), color-stop(#9b1827), to(#9b1827e5)) ;
    background: -webkit-linear-gradient(bottom, #9b1827e5, #9b1827, #9b1827e5) ;
    background: -o-linear-gradient(bottom, #9b1827e5, #9b1827, #9b1827e5) ;
    background: linear-gradient(to top, #9b1827e5, #9b1827, #9b1827e5) ;
}

.navbar-light{
    color: ghostwhite;
}

.navbar-item{
    padding-right: 15px;
}

.navbar-title{
    font-family: 'Roboto' sans-serif;
    font-size: 24px;
    margin: auto 0;
}



